// @flow
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { ORDER_EDIT_COSTS } from '../../../../queries/OrderEditCosts';
import { Form } from 'formik';
import Modal from '../../../../components/Modal/ChargeModal';
import { CardNumberElement } from '@stripe/react-stripe-js';
import { useStripe, useElements } from '@stripe/react-stripe-js';
import Button from '../../../../components/Button';
import { displayFlex } from '../../../../styles/Mixins';
import colors from '../../../../styles/Colors';
import type { OrderType } from 'src/queries/Admin/OrderForOrderTableDetail.js';
import { Formik } from 'formik';
import { createStripeToken } from '../../shared/sharedMethods';

import { useLazyQuery } from 'react-apollo';
import { Grid, Radio, RadioGroup, FormControlLabel, CircularProgress } from '@material-ui/core';

import PaymentSection from '../../shared/ChargePaymentSection';

type ChargeModalBasePropsType = {|
  className?: string,
  onClose: () => void,
  open: boolean,
  order: OrderType,
  currentCharges?: number,
  handleSubmit?: (paymentDetails: {}, refundPayment: boolean, isGroupOrder: boolean, noRefund: {}) => void
|};

export const ChargeModalBase = (props: ChargeModalBasePropsType) => {
  const { className = '', onClose, open, order = {}, currentCharges = 0, handleSubmit } = props;
  const [getFees, { data: costFees }] = useLazyQuery(ORDER_EDIT_COSTS);
  const [fees, setCostFees] = useState(0);
  const [isLoading, setLoading] = useState(false);
  const [isCardPayment, setCardPayment] = useState(true);
  const elements = useElements();
  const stripe = useStripe();

  const [newCard, setNewCard] = useState(false);

  useEffect(() => {
    getFees({
      variables: {
        input: {
          orderId: +order.id,
          isNonUSCard: false,
          useCard: isCardPayment,
          amount: currentCharges
        }
      }
    });
    if (costFees) {
      setCostFees(costFees.orderCostsFee.fee);
    }
  }, [currentCharges, costFees, isCardPayment]);

  const formatPrice = price => {
    if (price < 0) return `-$${Math.abs(price).toFixed(2)}`;
    return `$${price.toFixed(2)}`;
  };

  const buildStripeToken = async values => {
    const cardInfo = {
      card: elements.getElement(CardNumberElement),
      name: values.nameOnCard,
      zip: values.zipCode
    };
    const stripeToken = await createStripeToken(stripe, cardInfo);
    return stripeToken;
  };

  return (
    <Modal heading={'Payment Method'} open={open} className={`${className}__charge-modal`}>
      <Formik
        onSubmit={async ({ ccInformation, adminId }) => {
          try {
            setLoading(true);
            if (ccInformation.selectedCard) {
              const paymentInput = {
                token: '',
                description: 'payment description',
                saveCard: ccInformation.saveCard,
                selectedCard: ccInformation.selectedCard,
                useCard: ccInformation.useCard,
                isNonUSCard: false,
                adminId
              };

              await handleSubmit(null, false, ccInformation.groupBill, null, paymentInput);
              onClose();
              setLoading(false);
            } else if (ccInformation.useCard && !ccInformation.selectedCard) {
              const stripeToken = await buildStripeToken(ccInformation);
              const paymentInput = {
                token: stripeToken ? stripeToken.token.id : '',
                description: 'payment description',
                saveCard: ccInformation.saveCard,
                selectedCard: stripeToken.token.card.last4,
                useCard: ccInformation.useCard,
                isNonUSCard: false,
                adminId
              };
              await handleSubmit(null, false, ccInformation.groupBill, null, paymentInput);
              onClose();
              setLoading(false);
            } else {
              const paymentInput = {
                token: '',
                description: 'payment description',
                saveCard: ccInformation.saveCard,
                selectedCard: null,
                useCard: ccInformation.useCard,
                isNonUSCard: false,
                adminId
              };
              await handleSubmit(null, false, ccInformation.groupBill, null, paymentInput);
              onClose();
              setLoading(false);
            }
          } catch (error) {
            setLoading(false);
          }
        }}
        initialValues={{
          ccInformation: {
            nameOnCard: null,
            saveCard: false,
            selectedCard: null,
            zipCode: null,
            useCard: true,
            stripeToken: null,
            groupBill: false,
            selectedCardBrand: ''
          },
          adminId: null,
          renterInformation: { ...order.user, savedCreditCards: [] },
          deferredGroupId: null,
          event: order.event
        }}>
        {({ dirty, isValid, values, setFieldValue }) => (
          <Form>
            <RadioGroup id="payment-details-radio-group" aria-label="reservationType" name="reservationType" className={`${props.className}__radio-group`}>
              <Grid container spacing={2}>
                <Grid item xs>
                  <FormControlLabel
                    id="credit-debit-control"
                    value="creditOrDebitCard"
                    control={<Radio color="primary" id={`radio-credit-debit`} />}
                    checked={values.ccInformation.useCard}
                    onChange={() => {
                      setFieldValue('deferredGroupId', null);
                      setFieldValue('ccInformation.useCard', true);
                      setFieldValue('ccInformation.groupBill', false);
                      setCardPayment(true);
                    }}
                    className={`${props.className}__payment-option`}
                    label={`Credit/Debit Card`}
                  />
                </Grid>
                <Grid item xs>
                  <FormControlLabel
                    id="cash-control"
                    value="cash"
                    control={<Radio color="primary" id={`radio-cash`} />}
                    checked={!values.ccInformation.useCard && !values.ccInformation.groupBill}
                    onChange={() => {
                      setFieldValue('deferredGroupId', null);
                      setFieldValue('ccInformation.useCard', false);
                      setFieldValue('ccInformation.groupBill', false);
                      setCardPayment(false);
                    }}
                    className={`${props.className}__payment-option`}
                    label={`Cash or Check`}
                  />
                </Grid>
              </Grid>
            </RadioGroup>
            {values.ccInformation.useCard && (
              <PaymentSection {...props} user={values.renterInformation} setNewCard={setNewCard} newCard={newCard} fees={fees} />
            )}
            {!values.ccInformation.useCard && (
              <div>
                <p className="_total-charge-text">
                  Total including fees: <strong>{formatPrice(currentCharges + fees)}</strong>
                </p>
              </div>
            )}

            <FlexButtonWrapper>
              <FormButton secondary variant="contained" size="large" onClick={onClose}>
                GO BACK
              </FormButton>
              <FormButton
                primary
                className={`${className}__charge-button`}
                variant="contained"
                size="large"
                type="submit"
                disabled={isLoading || !dirty || !isValid}>
                CHARGE
                {isLoading && <CircularProgress size={24} className="progress-spinner" />}
              </FormButton>
            </FlexButtonWrapper>
          </Form>
        )}
      </Formik>
    </Modal>
  );
};

const ChargeModal = styled(ChargeModalBase)`
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: space-around;

  &__field {
    margin: 10px 0 !important;
  }
  &__charge-modal {
    & {
      div[class^='MuiCard-root'],
      div[class*='MuiCard-root'] {
        max-width: 540px;
        font-size: 16px;
      }
      .MuiPaper-root.MuiCard-root {
        h4 {
          margin-top: 0;
          margin-bottom: 5px;
        }
      }
    }

    _total-charge-text {
      font-size: 16px;
      font-family: 'IBM Plex Sans';

      span {
        fontweight: bold;
      }
    }
  }
  &__no-fees-text {
    font-size: 12px;
  }
  &__original-payment-method.MuiGrid-item.MuiGrid-grid-xs-12 {
    padding: 20px 8px 0;
    p {
      margin: 0;
      margin-top: 5px;
      padding: 0;
      &:last-child {
        margin-top: 20px;
      }
    }
  }
  &__charge-actions.MuiGrid-item.MuiGrid-grid-xs-12 {
    padding: 20px 8px 10px;
  }
  &__charge-field-row.MuiGrid-item.MuiGrid-grid-xs-12 {
    padding: 0 8px;
    a {
      color: ${colors.secondary};
      text-decoration: underline;
      &:hover {
        cursor: pointer;
      }
    }
    .MuiTextField-root {
      &:last-child {
        margin: 10px 0 0;
      }
    }
  }
  &__charge-method {
    margin-bottom: 0;
  }
  &__charge-button {
    padding: 8px 16px !important;
  }
  &__radio-group {
    flex-wrap: nowrap;
    margin-bottom: -5px;
    white-space: nowrap;
    width: 80%;
  }

  &__payment-option-heading {
    &&& {
      margin-top: 15px;
    }
  }

  &__card-details-heading {
    &&& {
      margin-top: 20px;
      margin-bottom: 10px;
    }
  }

  &__payment-option {
    &&& {
      span {
        font-family: 'IBMPlexSans-Regular';
        font-size: 16px;
      }
    }
  }
`;

const FlexWrapper = styled.div`
  ${displayFlex}
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  &__field {
    width: 250px;
  }
  @media screen and (max-width: 601px) {
    ${displayFlex}
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
`;

const FlexButtonWrapper = styled(FlexWrapper)`
  ${displayFlex}
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  @media screen and (max-width: 601px) {
    ${displayFlex}
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
  }
`;

const FormButton = styled(Button)`
  &&& {
    line-height: 0;
    width: 120px;
    margin-left: ${props => (props.primary ? 20 : 0)}px;
  }
`;

export default ChargeModal;
