import React, { useState } from 'react';
import EventReportPreview from '../../../assets/img/reports/EventReport.png';
import ReconciliationPreview from '../../../assets/img/reports/Reconciliation.png';
import StallReportPreview from '../../../assets/img/reports/StallReport.png';
import RVReportPreview from '../../../assets/img/reports/RVReport.png';
import AccountingReportPreview from '../../../assets/img/reports/AccountingReport.png';
import { HeadingThree } from 'Components/Headings';
import upperFirst from 'Utils/upperFirst';
import { Report } from 'Constants/reportType';
import AccountingReconTransReportModal from './AccountingReconTransReportModal';
import EventAndStallReport from './EventAndStallReportModal';

const ReportsList: React.FC = () => {
  const [reportTypeOpen, setReportTypeOpen] = useState('');

  const RenderReport = () => {
    if (!reportTypeOpen) return <></>;
    switch (reportTypeOpen) {
      case Report.EVENT:
        return <EventAndStallReport isOpen handleClose={handleCloseModal} onCancel={handleCloseModal} header="Event Report" />;
      case Report.STALL:
        return <EventAndStallReport isOpen handleClose={handleCloseModal} onCancel={handleCloseModal} header="Stall &amp; Add On Report" stallReport />;
      case Report.RV:
        return <EventAndStallReport isOpen handleClose={handleCloseModal} onCancel={handleCloseModal} header="RV Report" rvReport />;
      default:
        return (
          <AccountingReconTransReportModal
            isOpen
            handleClose={handleCloseModal}
            onCancel={handleCloseModal}
            header={`${upperFirst(reportTypeOpen)} Report`}
            reportType={reportTypeOpen}
          />
        );
    }
  };

  const handleCloseModal = () => {
    setReportTypeOpen('');
  };

  const financialReports = [
    // {
    //   name: 'Transaction Report',
    //   img: TransactionReportPreview,
    //   testid: 'transaction-report',
    //   onClick: () => setReportTypeOpen(Report.TRANSACTION)
    // },
    {
      name: 'Accounting Report',
      img: AccountingReportPreview,
      testid: 'accounting-report',
      onClick: () => setReportTypeOpen(Report.ACCOUNTING)
    },
    {
      name: 'Event Report',
      img: EventReportPreview,
      testid: 'event-report',
      onClick: () => setReportTypeOpen(Report.EVENT)
    },
    {
      name: 'Reconciliation Report',
      img: ReconciliationPreview,
      testid: 'reconciliation-report',
      onClick: () => setReportTypeOpen(Report.RECON)
    }
  ];
  const operationsReports = [
    {
      name: 'Stall & Add On Report',
      img: StallReportPreview,
      testid: 'stall-report',
      onClick: () => setReportTypeOpen(Report.STALL)
    },
    {
      name: 'RV Report',
      img: RVReportPreview,
      testid: 'rv-report',
      onClick: () => setReportTypeOpen(Report.RV)
    }
  ];

  return (
    <div className="reports-list-container">
      <HeadingThree label="Financial Reports" />
      <div className="financial-reports">
        {financialReports.map(report => {
          const { onClick, name, testid, img } = report;
          return (
            <div key={report.name} className="reports" onClick={onClick}>
              <img src={img} alt={name} width="164px" height="208px" data-testid={testid} />
              <p>{name}</p>
              <span>Financial Reports</span>
            </div>
          );
        })}
        <RenderReport />
      </div>
      <HeadingThree label="Operations Reports" />
      <div className="operations-reports">
        {operationsReports.map(report => {
          const { onClick, name, testid, img } = report;
          return (
            <div key={report.name} className="reports" onClick={onClick}>
              <img src={img} alt={name} width="164px" height="208px" data-testid={testid} />
              <p>{name}</p>
              <span>Operations Reports</span>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default ReportsList;
