import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Grid, Switch } from '@material-ui/core';
import { Field, Form, useFormikContext } from 'formik';
import { FormikField, FormikPhoneField } from '../../../../components/Fields';
import { FormSelect } from '../../../../components/Select';
import Button from '../../../../components/Button';
import colors from '../../../../styles/Colors';
import { CreateGroupFormInterface } from './types';
import states from './stateList';
import './CreateAndEditGroupModal.scss';
import { HeadingFive } from 'Components/Headings';

const GroupSwitch = withStyles({
  switchBase: {
    color: colors.white,
    '&$checked': {
      color: colors.white
    },
    '&$checked + $track': {
      backgroundColor: colors.button.primary.active
    }
  },
  checked: {},
  track: {}
})(Switch);

const CreateGroupModalForm: React.FC<CreateGroupFormInterface> = ({ isLoading, onClose }) => {
  const { values, setFieldValue, isSubmitting, errors }: any = useFormikContext();
  const { allowDeferred } = values;
  const [selectedState, setSelectedState] = useState('');
  const [stateTouched, setStateTouched] = useState(false);

  const hasFormErrors = () => {
    if (Object.keys(errors).length > 0) return true;
    for (const key in values) {
      if (values[key] === '') return true;
    }
    return false;
  };

  const handleDeferredPaymentToggle = () => setFieldValue('allowDeferred', !allowDeferred);

  return (
    <Form>
      <Grid container spacing={0}>
        <Grid item xs={10}>
          <div className="toggle-container">
            <GroupSwitch color={'primary'} disabled={false} onChange={handleDeferredPaymentToggle} size={'medium'} checked={allowDeferred} />
            <div className="toggle-text-container">
              <p>Allow Deferred Payment</p>
              <p>Turning on will add the group to deferred payment options</p>
            </div>
          </div>
        </Grid>
      </Grid>
      <div className="section-header">
        <HeadingFive label="GROUP INFORMATION" />
      </div>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Field autoComplete="none" label="GROUP NAME" type="text" name="name" variant="filled" component={FormikField} />
        </Grid>
      </Grid>
      <div className="section-header">
        <HeadingFive label="CONTACT INFORMATION" />
      </div>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <Field autoComplete="none" label="PRIMARY CONTACT NAME" type="text" name="contactName" variant="filled" component={FormikField} />
        </Grid>
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Field autoComplete="none" label="EMAIL ADDRESS" type="email" name="email" variant="filled" component={FormikField} />
        </Grid>
        <Grid item xs={6}>
          <Field name="phone" autoComplete="none">
            {({ field, meta }: any) => (
              <FormikPhoneField
                {...field}
                {...meta}
                autoComplete="none"
                error={meta.touched && !!meta.error}
                helperText={meta.touched && !!meta.error && meta.error}
                touched={`${meta.touched}`}
                label="PHONE NUMBER"
                variant="filled"
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFieldValue('phone', e.target.value.replace(/[^0-9]/g, ''))}
              />
            )}
          </Field>
        </Grid>
      </Grid>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <Field autoComplete="none" label="ADDRESS" type="text" name="street" variant="filled" component={FormikField} />
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Field autoComplete="none" label="CITY" type="text" name="city" variant="filled" component={FormikField} />
        </Grid>
        <Grid item xs={3}>
          <Field name="state" autoComplete="none">
            {({ field, meta }: any) => (
              <FormSelect
                {...field}
                {...meta}
                onBlur={() => setStateTouched(true)}
                error={stateTouched && !values.state ? 'State is required' : ''}
                selectedOption={selectedState || values.state?.toUpperCase()}
                label="STATE"
                cb={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setFieldValue('state', e.target.value);
                  setSelectedState(e.target.value);
                }}
                className={`state-select ${selectedState ? 'filled' : ''}`}
                options={states}
              />
            )}
          </Field>
        </Grid>
        <Grid item xs={3}>
          <Field
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              if (e.target.value.length < 6) setFieldValue('zipCode', e.target.value.replace(/[^0-9,']/g, ''));
            }}
            autoComplete="none"
            label="ZIP"
            type="text"
            name="zipCode"
            variant="filled"
            component={FormikField}
          />
        </Grid>
      </Grid>
      <div className="flex-button-wrapper">
        <Button className="form-button" secondary variant="contained" size="small" onClick={onClose} data-testid="create-group-cancel">
          CANCEL
        </Button>
        <Button
          className="form-button"
          primary
          onClick={() => setFieldValue('isSubmitting', true)}
          variant="contained"
          size="small"
          type="submit"
          disabled={isSubmitting || isLoading || hasFormErrors()}
          data-testid="create-group-save">
          SAVE
        </Button>
      </div>
    </Form>
  );
};

export default CreateGroupModalForm;
