import gql from 'graphql-tag';

export const GROUP_BY_ID = gql`
  query getGroupById($id: ID) {
    group(id: $id) {
      id
      contactName
      name
      street
      city
      state
      zipCode
      phone
      email
      allowDeferred
      orders {
        orderId
      }
    }
  }
`;
