//@flow
import React, { useEffect } from 'react';
import { useFormikContext } from 'formik';

import type { EventFormType } from './FormTypes';

type CreateEventFormPropsType = {|
  children: React$Node,
  className?: string,
  isLoading: boolean
|};

export const CreateEventForm = ({ children, className, isLoading }: CreateEventFormPropsType) => {
  const { setFieldValue } = useFormikContext<EventFormType>();
  useEffect(() => {
    setFieldValue('isLoading', isLoading);
  }, [isLoading]);
  return <form className={className}>{children}</form>;
};
